header {
	margin: 0;
	padding: 0;
	width: 100%;
	background: #fff;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 5;
}
header p {
	width: 95%;
	margin: 0 auto;
	font-size: 1.4rem;
	font-weight: lighter;
	padding-top: 15px;
	padding-bottom: 15px;
}
.menu-item {
	color:#fff;
	text-decoration: none;
	margin-top: .5rem;
	font-weight: lighter;
	font-size: 1.4rem;
	font-weight: lighter;
}
.is-active  {
	color: #249cff;
}
/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  width: 25px;
  height: 15px;
	top: 20px;
  right: 15px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
	background: #000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #272832;
  padding: 2.5em 1.5em 0;
	overflow-y: hidden !important;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #171717;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
@media screen and (min-width: 768px){
	header p {
		font-size: 1.6rem;
	}
}
@media screen and (min-width: 1024px){
	header p {
		width: 90%;

	}
}
@media only screen and (min-width: 1280px){
	.bm-burger-button {
	  position: absolute;
	  width: 25px;
	  height: 15px;
		top: 25px;
	  right: 25px;
	}
}
@media screen and (min-width: 1366px){
	header p {
		width: 70%;
	}
}
@media screen and (min-width: 1440px){
	header p {
		width: 60%;
	}
}



