
footer {
	background-color: #fff;
	position: relative;
	padding-top: 50px;
	padding-bottom: 50px;

}
footer i {
	color: #cfcfcf;
	font-size: 1.2rem;
	margin: 20px 10px;
}
footer i:hover {
	color: #000;
}
footer p {
	text-align: center;
	color: #cfcfcf;
	margin: 0;
}
#social-icons {
	width: 100%;
	text-align: center;
}

@media screen and (min-width: 768px){
	#footer-container {
		width: 95%;
		margin: 0 auto;
	}
	#footer-container p {
		width: 33.333%;
		float: left;
		text-align: left;
	}
	#social-icons {
		width: 66.666%;
		float: right;
		text-align: right;
	}
	footer i {
		margin: 0 15px;
	}
	footer i:last-child {
		margin-right: 0;
	}
}
@media screen and (min-width: 1024px){
	#footer-container {
		width: 90%;
	}

}
@media screen and (min-width: 1366px){
	#footer-container {
		width: 70%;
	}

}
@media screen and (min-width: 1440px){
	#footer-container {
		width: 60%;
	}

}



