* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
body {
	margin: 0;
	padding: 0;
}
#contact-page {
	max-width: 1920px;
	min-width: 320px;
	font-family: 'Roboto';

}
#contact-background {
	background-image: url('../images/background.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 170px;
}
#contact-h1 {
	font-size: 2rem;
	font-weight: lighter;
	color: #fff;
	width: 95%;
	margin: 0 auto;
	padding-top: 90px;
}
#contact2 {
	padding-top: 25px;
	padding-bottom: 25px;
	background: #ededed;
}
#contact-container2 {
	width: 95%;
	margin: 0 auto;
}
#contact-h2 {
	font-size: 2rem;
	font-weight: lighter;
	color: #696979;
  margin: 0;

}
#contact-container2 p {
	font-weight: lighter;
	color: #696979;
	font-size: .95rem;
}
@media screen and (min-width: 768px){
  #contact-background {
		height: 290px;
	}
	#contact-h1 {
		font-size: 2.6rem;
		padding-top: 140px;
	}
  #contact2 {
  	padding-top: 50px;
  	padding-bottom: 50px;
  }
  #contact-h2 {
		width: 33.333%;
		float: left;
	}
	#contact-container2 p {
		width: 66.666%;
		float: right;
	}
}
@media screen and (min-width: 1024px){
	#contact-h1 {
		font-size: 2.8rem;
		padding-top: 140px;
		width: 90%;
	}
  #contact-container2 {
		width: 90%;
	}
}
@media screen and (min-width: 1366px){
  #contact-h1 {
		width: 70%;
	}
	#contact-container2 {
		width: 70%;
	}

}
@media screen and (min-width: 1440px){
  #contact2 {
  	padding-top: 75px;
  	padding-bottom: 75px;
  }
  #contact-h1 {
		width: 60%;
	}
	#contact-container2 {
		width: 60%;
	}

}



