
* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
body {
	margin: 0;
	padding: 0;
}
#home-page {
	max-width: 1920px;
	min-width: 320px;
	font-family: 'Roboto';
	background: #ededed;
}
#background {
	background-image: url('../images/background.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 365px;
}
#intro {
	width: 95%;
	margin: 0 auto;
	padding-top: 90px;
}
#hello{
	font-size: 2rem;
	font-weight: lighter;
	color: #fff;
}
#viewP {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	padding: 0;
	border: 2px solid #fff;
	width: 153px;
	height: 45px;
	color: #fff;
	font-size: 1.2rem;
	font-weight: 400;
}
#viewP:hover {
	cursor: pointer;
	color: #000;
	border: 2px solid #000;

}
#wrapper {
	width: 95%;
	margin: 0 auto;

}
#about {
	padding-top: 25px;
	padding-bottom: 25px;
}
#home-h2 {
	font-size: 2rem;
	font-weight: lighter;
	color: #696979;
	margin: 0;
}
#home-intro {
	font-weight: lighter;
	color: #696979;
	margin: 15px 0 15px 0;
	font-size: .95rem;
	line-height: 1.2rem;
}
.white {
	background: #fff;
	font-size: .9rem;
}
.skills {
	color: #696979;
}
.html {
	background: #cfcfcf;
	padding: 10px;
	width: 80%;
}
.css {
	background: #cfcfcf;
	padding: 10px;
	width: 90%;
}
.js {
	background: #cfcfcf;
	padding: 10px;
	width: 45%;
}
.jquery {
	background: #cfcfcf;
	padding: 10px;
	width: 20%;
}
.php {
	background: #cfcfcf;
	padding: 10px;
	width: 50%;
}
.react {
	background: #cfcfcf;
	padding: 10px;
	width: 25%;
}
.mysql {
	background: #cfcfcf;
	padding: 10px;
	width: 70%;
}
.photoshop {
	background: #cfcfcf;
	padding: 10px;
	width: 60%;
}
.seo {
	background: #cfcfcf;
	padding: 10px;
	width: 65%;
}
#contact {
	padding-top: 50px;
	padding-bottom: 50px;
}
#contact-container {
	width: 95%;
	margin: 0 auto;
}
h4 {
	font-size: 2rem;
	font-weight: lighter;
	color: #696979;
	margin: 0;
}
#contact-container p {
	font-weight: lighter;
	color: #696979;
	font-size: .95rem;
}

@media screen and (min-width: 768px){
	#background {
		height: 600px;
	}
	#intro {
		padding-top: 200px;
	}
	#hello{
		font-size: 2.7rem;
	}
	#viewP {
		width: 167px;
		height: 50px;
	}
	#home-h2 {
		width: 33.333%;
		float: left;
	}
	#home-intro {
		width: 66.666%;
		float: right;
	}
	#skills-container {
		width: 66.666%;
		clear: both;
		margin-left: 33.333%;
	}
	h4 {
		width: 33.333%;
		float: left;
	}
	#contact-container p {
		width: 66.666%;
		float: right;
	}
}
@media screen and (min-width: 1024px){
	#intro {
		width: 90%;
	}
	#wrapper {
		width: 90%;
	}
	#contact-container {
		width: 90%;
	}

}
@media screen and (min-width: 1366px){
	#intro {
		width: 70%;
	}
	#hello {
		font-size: 2.8rem;
	}
	#wrapper {
		width: 70%;
	}
	#contact-container {
		width: 70%;
	}

}
@media screen and (min-width: 1440px){
	#intro {
		width: 60%;
	}
	#about {
		padding-top: 50px;
		padding-bottom: 50px;
	}
	#wrapper {
		width: 60%;
	}
	#contact-container {
		width: 60%;
	}

}



