* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
body {
	margin: 0;
	padding: 0;
}
#about-page {
	max-width: 1920px;
	min-width: 320px;
	font-family: 'Roboto';

}
#about-background {
	background-image: url('../images/background.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 170px;
}
#about-h1 {
	font-size: 2rem;
	font-weight: lighter;
	color: #fff;
	width: 95%;
	margin: 0 auto;
	padding-top: 90px;
}
#about2 {
	padding-top: 25px;
	padding-bottom: 25px;
	background: #ededed;
}
#about-h2 {
	font-size: 2rem;
	font-weight: lighter;
	color: #696979;
	width: 95%;
	margin: 0 auto;
}
#about-intro {
	width: 95%;
	font-weight: lighter;
	color: #696979;
	margin: 15px auto;
	font-size: .95rem;
	line-height: 1.2rem;
}
img {
	display: block;
	width: 95%;
	margin: auto;
	height: auto;
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
}
#mobile2 {
	display: none;
}
#mobile3 {
	display: none;
}
#about-wrapper {
	width: 95%;
	margin: 0 auto;
}
#process {
	background: #fff;
	padding-top: 25px;
	padding-bottom: 25px;
}
#about-h3 {
	font-size: 2rem;
	font-weight: lighter;
	color: #696979;
	margin: 0;
}
.process-intro {
	color: #249cff;
	font-size: 1.2rem;

}
.process-desc {
	font-weight: lighter;
	color: #696979;
	margin: 15px 0 15px 0;
	font-size: .95rem;
}
#hire-me {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	height: 20vh;
}
#hire-button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	padding: 0;
	border: 2px solid #000;
	width: 117px;
	height: 53px;
	color: #000;
	font-size: 1.2rem;
	font-weight: 400;
	text-decoration: none;
}
#hire-button:hover {
	border: 2px solid #249cff;
	color: #249cff;
	cursor: pointer;
}
@media screen and (min-width: 768px){
	#about-background {
		height: 290px;
	}
	#about-h1 {
		font-size: 2.6rem;
		padding-top: 140px;
	}
	#about2 {
		display: grid;
    grid-template-columns:repeat(6, 2fr);
    grid-template-rows:repeat(2, minmax(0px, auto));
		padding: 0;

	}
	#about-row1 {
		grid-column: 1/4;
		padding-top: 75px;
		padding-bottom: 75px;

	}
	#about-h2 {
		width: 90%;

	}
	#about-intro {
		width: 90%;
		margin: 25px auto;
	}
	#about-row2 {
		grid-column: 4/7;
	}
	img {
		width: 355px;
		height: 391px;
		margin: 0 15px;
	}
	#mobile1 {
		display: none;
	}
	#mobile2 {
		display: unset;
	}
	#about-h3 {
		padding-bottom: 10px;
	}
	#process-outer-container {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
	}
	.process-container {
		width: 50%;
	}
	.process-intro {
		margin-bottom: 5px;
	}
	.process-desc {
		margin: 0 0 15px 0;
	}
	#process {
		padding-top: 75px;
		padding-bottom: 75px;
	}

}
@media screen and (min-width: 1024px){
	#about-h1 {
		font-size: 2.8rem;
		padding-top: 140px;
		width: 90%;
	}
	#about-h2 {
		width: 80%;
	}
	#about-intro {
		width: 80%;

	}
	#mobile2 {
		display: none;
	}
	#mobile3 {
		display: unset;
	}
	img {
		width: 461px;
		height: 391px;
		margin: 0;
	}
	#about-wrapper {
		width: 90%;
	}

}
@media screen and (min-width: 1366px){
	#about-h1 {
		width: 70%;
	}
	#about-h2 {
		width: 40%;
	}
	#about-intro {
		width: 40%;
	}
	#about-wrapper {
		width: 70%;
	}


}
@media screen and (min-width: 1440px){
	#about-h1 {
		width: 60%;
	}
	#about-h2 {
		margin: 0 0 0 40%;
	}
	#about-intro {
		margin: 25px 0 0 40%;
		width: 50%;
	}
	#about-wrapper {
		width: 60%;
	}


}



