#projects {
	background: #fff;
	width: 100%;
}
#projects-h3 {
	padding-top: 25px;
	padding-bottom: 25px;
	width: 95%;
	margin: 0 auto;
	font-size: 2rem;
	font-weight: lighter;
	color: #696979;
}
#project1 {
	background-image: url('../images/project1.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 350px;
	position: relative;
}
.overlay {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	background: #000;
	opacity: .3;
	width: 100%;
	height: 100%;
}
.overlay:hover {
	background: #ff0000;
	opacity: .4;
}
.project-desc {
	position: absolute;
	bottom: 5px;
	left: 15px;
	z-index: 4;
	color: #fff;
	font-size: 1.3rem;
	font-family: 'Roboto';
	font-weight: lighter;

}
.project-desc p, small {
	margin: 0;
}
small {
	color: #fff;
	font-size: .8rem;
	letter-spacing: 2px;
}

#project2 {
	background-image: url('../images/project3.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 350px;
	position: relative;
}
#project3 {
	background-image: url('../images/project4.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 350px;
	position: relative;
}
#project4 {
	background-image: url('../images/project2.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 350px;
	position: relative;
}
#project1, #project2, #project3, #project4:hover {
	cursor: pointer;
}
@media screen and (min-width: 768px){
	#projects-grid {
    display: grid;
    grid-template-columns:repeat(6, 2fr);
    grid-template-rows:repeat(4, minmax(0px, auto));
  }
	#project1 {
		grid-column: 1/4;
	}
	#project2 {
		grid-column: 4/7;
	}
	#project3 {
		grid-column: 1/4;
	}
	#project4 {
		grid-column: 4/7;
	}
}
@media screen and (min-width: 1024px){
	#projects-h3 {
		width: 90%;
	}

}
@media screen and (min-width: 1366px){
	#projects-h3 {
		width: 70%;
	}

}
@media screen and (min-width: 1440px){
	#projects-h3 {
		width: 60%;
	}

}



