form {
	width: 100%;
	margin: 0 auto;
}
input, textarea {
	width: 100%;
	padding: 20px;
	border: none;
	font-size: 1rem;
	color: #696979;
	font-family: 'Roboto';
	font-weight: lighter;

}
input:nth-child(odd) {
	background: #f7f7f7;
}
textarea {
	height: 100px;
}
#submit {
	background: #cfcfcf;
	color: #fff;
	width: auto;
	padding: 20px;
}
#submit:hover {
	background: #249cff;
	cursor: pointer;
}
@media screen and (min-width: 768px){
	form {
		width: 66.666%;
		clear: both;
		margin-left: 33.333%;
	}
}



